/**
 * Bio component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React from "react";
import marked from "marked";
import { withConfig } from "../../api/config";
import "./Bio.scss";
import Socials from "./Socials";

function Bio(props) {
  const { info } = props;
  return (
    <section className="m-info" id="about">
      <div className="container">
        <div className="row">
          <div className="col-lg-3 col-md-4 col-sm-5 col-8 mx-auto mt-2" style={{ textAlign: "center" }}>
            <div className="profile-wrapper">
              <a href={info.profile_img}>
                <img src={info.profile_img} alt="" />
              </a>
            </div>
            <div className="mt-2">
              <Socials detail />
            </div>
          </div>

          <div className="col-lg-9 col-md-8 col-sm-7 col-12">
            <h2 dangerouslySetInnerHTML={{ __html: marked.inlineLexer(info.full_name, []) }} />
            {info.job_title && (
              <h5
                dangerouslySetInnerHTML={{
                  __html: marked.inlineLexer(info.job_title, [])
                }}
              />
            )}
            {info.headline && (
              <h5
                dangerouslySetInnerHTML={{
                  __html: marked.inlineLexer(info.headline, [])
                }}
              />
            )}
            {info.affiliation && (
              <h5
                dangerouslySetInnerHTML={{
                  __html: marked.inlineLexer(info.affiliation, [])
                }}
              />
            )}
            <div dangerouslySetInnerHTML={{ __html: marked(info.about) }} />
          </div>
        </div>
      </div>
    </section>
  );
}

export default withConfig((props) => {
  const info = props.config.info;
  return <Bio info={info} {...props} />;
});
